import React from "react";
import WPlayer from "./WPlayer";
import { HashRouter, Route, Switch } from "react-router-dom";
import GlobalStyle from "../styles/GlobalStyle";

const App = () => (
  <HashRouter>
    <>
      <Switch>
        <Route exact path="/" component={WPlayer} />
        <Route exact path="/:activeVideo" component={WPlayer} />
      </Switch>
      <GlobalStyle />
    </>
  </HashRouter>
);

export default App;
